const CalibrateButton = ({ onClick }: { onClick: () => void }) => {
  return (
    <button
      onClick={(e) => {
        e.stopPropagation();
        onClick();
      }}
      className="flex h-8 w-fit items-center justify-center rounded-xl border-2 border-white p-4 text-white opacity-20"
    >
      Calibrate
    </button>
  );
};

const PidSettings = () => {
  const { t } = useTranslation();

  const [expanded, setExpanded] = useState(false);
  const [isOpen, setModelOpen] = useState(false);

  const toggleExpand = () => {
    setExpanded(!expanded);
    console.log("toggle expand");
  };
  const cardRef = useRef<HTMLDivElement>(null);
  useEffect(() => {
    if (!expanded && cardRef.current) {
      // cardRef.current.scrollIntoView({ behavior: "smooth" });
      const navBarHeight = parseInt(
        getComputedStyle(document.documentElement).getPropertyValue(
          "--navbar-height",
        ),
        10,
      );
      const topPosition =
        cardRef.current.getBoundingClientRect().top - navBarHeight;
      // window.scrollTo({ top: topPosition, behavior: "smooth" });
    }
  }, [expanded]);

  return (
    <Card
      ref={cardRef}
      className={
        "w-full max-w-2xl transform bg-gradient-to-r from-purple-600 to-red-600 transition duration-500 "
      }
    >
      <CardHeader
        className="flex h-16 flex-row items-center justify-between"
        onClick={toggleExpand}
      >
        <div className="flex items-center">
          <FaTemperatureHigh className="ml-4 text-white" />
          <h2 className="mx-4 font-bold text-white">{t("Heater")}</h2>
        </div>
        {expanded ? (
          <>
            <MdKeyboardArrowDown className="mr-4 text-white" />
          </>
        ) : (
          <MdKeyboardArrowRight className="mr-4 text-white" />
        )}
      </CardHeader>
      {expanded ? (
        <>
          <CardBody className="flex w-full flex-col justify-center space-y-4 py-4">
            <PidConfigurationComponents index={0} />
            <PidConfigurationComponents index={1} />
            <PidConfigurationComponents index={2} />
          </CardBody>
          <CardFooter
            onClick={toggleExpand}
            className="align-center justify-center"
          >
            <MdKeyboardArrowUp className="m-4 text-white" />
          </CardFooter>
        </>
      ) : null}

      {isOpen && (
        <Modal
          title="Calibrate flowmeter"
          size="small"
          onClose={() => setModelOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => setModelOpen(false),
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setModelOpen(false),
          }}
          autoFocus={false}
        >
          <Inset space={4}>
            <Body size="medium">Content</Body>
          </Inset>
        </Modal>
      )}
    </Card>
  );
};

